.myPosts{
    padding: 10px;
}
.box{
    display: flex;
}
.myPosts textarea{
    border: 1px solid rgb(44, 111, 199);
    border-radius: 5px;
    margin-right: 15px;
    font-size: 20px;
}

.myPosts button{
    padding: 10px 45px;
    background: chartreuse;
    border: none;
    border-radius: 5px;
    margin: auto;
    box-shadow: 1px 2px 3px rgb(44, 111, 199);
    cursor: pointer;
}